import { Value } from '@ali/4ever-cangjie';

export default (value: Value) => {
  const { document, selection } = value;

  if (!selection) {
    return false;
  }
  const start = selection.getStart(document);

  if (!start.key) {
    return false;
  }
  const closeInline = document.getClosestInline(start.key);
  if (closeInline && closeInline.type === 'tag') return true;

  return value.inlines.some(inline => inline.type === 'tag');
};
